<template> 
  <div class="message">
    <van-nav-bar    
      title="我的消息"
      fixed
      @click-left="$toPage(-1)"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="main" v-for="(item,index) in list" :key="index">
      <div class="header">
        <span class="title">【一块医药】</span>
        <span class="time">{{ item.time }}</span>
      </div>
      <div class="content">
        <img src="../../assets/me_icon/b4ac3a6.png" />
        <div>{{ item.msg }}</div>
      </div>
    </div>
  </div>
</template>
<script>
import * as types from "../../store/mutations";

export default {
  data() {
    return {
      list: []
    }
  },
  computed: {
  },
  mounted() {
    this.getMessage()
  },
  methods: {
    getMessage() {
      JNB.query({
        serverName: 'orderServer'+ this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: 'PushMessageModule',
        method: 'queryMessage',
        callback: (data)=>{

          let result = JSON.parse(data)
          if (result.code == 200) {
          this.list = result.data
            //移除消息
            this.$store.commit(types.SET_MESSAGE_LIST, [])
          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    }
  }
}
</script>
<style scoped>
.message{
  background-color: #f8f8f8;
  width: 100%;
  height: 100%;
  padding-top: 5.6vh;
}
.message .main{
  width: 670px;
  margin: 20px auto 0;
  background-color: #fff;
  border-radius: 20px;
  padding: 20px 20px 5px;
  overflow: hidden;
}
.message .main .header{
  margin: 20px 20px 35px;
  font-size:32px;
  font-weight:bold;
}
.message .main .header .time{
  float: right;
  font-size: 28px;
  font-weight: 400;
  color: rgba(153,153,153,1);
}
.message .main .content{
  padding: 0 20px 20px;
  font-size:28px;
  overflow: hidden;
}
.message .main .content img{
  width: 90px;
  height: 90px;
  padding: 15px;
}
.message .main .content div{
  float: right;
  margin-right: 20px;
  width: 490px;
  overflow: hidden;
}
.van-nav-bar .van-icon{
  color: #666666;
}
  .van-nav-bar{
    background-color: #ffffff;
  }
</style>
